import React, { useState, useCallback, useEffect } from 'react';

const LoginComponent = () => {
  const [formData, setFormData] = useState({ username: '', password: '' });
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [loading, setLoading] = useState(false);

  // Check if there's a valid login state in localStorage on component mount
  useEffect(() => {
    const loggedInUser = localStorage.getItem('qrtrasau');
    if (loggedInUser) {
      setSuccess('Bn  ng nhp ri!');
    }
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = useCallback((e) => {
    window.location.reload();
    e.preventDefault();
    setLoading(true);

    // Kim tra thng tin ng nhp
    const { username, password } = formData;
    if (username === 'qrtrasau' && password === 'trasau123') {
      // ng nhp thnh cng
      setSuccess('ng nhp thnh cng!');
      setError('');
      localStorage.setItem('qrtrasau', 'true'); // Save login state in localStorage

      // Reset form and loading state
      setFormData({ username: '', password: '' });
    } else {
      // ng nhp tht bi
      setError('Tn ng nhp hoc mt khu khng ng.');
      setSuccess('');
    }

    setLoading(false);
  }, [formData]);

  return (
    <div className=" flex justify-center items-center  bg-gray-50">
      <div className="p-10 rounded-lg">
        <h2 className="text-2xl font-bold text-center mb-4">ng Nhp</h2>
        {error && <p className="text-red-500 text-center">{error}</p>}
        {success && <p className="text-green-500 text-center">{success}</p>}
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700" htmlFor="username">
              Tn ng nhp
            </label>
            <input
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleChange}
              className="mt-1 block w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-cyan-500"
            />
          </div>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700" htmlFor="password">
              Mt khu
            </label>
            <input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              className="mt-1 block w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-cyan-500"
            />
          </div>
          <button
            type="submit"
            className="w-full bg-red-600 text-red-700 py-2 rounded-md hover:bg-cyan-600"
            disabled={loading}
          >
            {loading ? 'ang ng nhp...' : 'ng Nhp'}
          </button>
        </form>
        <p className='px-2 mt-4'>-  tip tc s dng dch v. Xin lin h zalo 0362061339  c cp ti khon s dng.Thank All !!! </p>
      </div>
    </div>
  );
};

export default LoginComponent;
